import { motion } from 'framer-motion';
import { useRef } from 'react';
import React from 'react';

const sectionVariant = {
  offscreen: {
    y: 200
  },
  onscreen: {
    y: 50,
    // rotate: -10,
    transition: {
      type: 'spring',
      bounce: 0.4,
      duration: 0.8
    }
  }
};

export default function SlideSection({ children }) {
  const ref = useRef(null);

  return (
    <motion.section
      className='w-full flex items-center justify-center mb-12'
      ref={ref}
      initial='offscreen'
      whileInView='onscreen'
      viewport={{ once: true, amount: 0.8 }}>
      <motion.div
        variants={sectionVariant}
        className='w-full p-4 flex flex-col justify-start items-start'>
        {children}
      </motion.div>
    </motion.section>
  );
}
